import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  makeAnAppointment (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey ? InitData.acKey : null,
      'passengerId': obj.passengerId,
      'passengerName': obj.passengerName,
      'passengerPhone': obj.passengerPhone,
      'companyId': obj.companyId,
      'channelName': obj.channelName,
      'couponId': obj.couponId,
      'serverTime': obj.serverTime,
      'budgetPay': obj.budgetPay,
      'startAddress': obj.startAddress,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'userPhone': obj.userPhone,
      'endAddress': obj.endAddress,
      'endLat': obj.endLat,
      'endLng': obj.endLng,
      'openId': obj.openId
    }
    var url = InitData.weixinUrl + '/api/daijia/create'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'passengerId ': obj.passengerId
    }
    var url = InitData.serverUrl + '/h5/v4/dj/getRunning'
    Vue.http.get(url, params).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  budgetCost (params, callback) { // 
    params = Vue.prototype.$utils.objKeySort('/passenger/api/v1/getBudgetPrice', params)
    var url = 'v1/getBudgetPrice'
    Vue.http.get(url, {params}).then(
      data => {
        // console.log(data)
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { //  
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/h5/v4/dj/findCouponV4'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  cancelOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'reson': obj.reson
    }
    var url = InitData.weixinUrl + '/h5/v4/dj/cancelOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  balancePay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'who': obj.who,
      'payType': obj.payType
    }
    var url = InitData.weixinUrl + '/h5/v4/dj/doPay'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  payInfo (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/dj/prepay'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  weixinPay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/daijia/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // ,apialertpclogpc
            appId: window.configure.WXappId, // 
            timeStamp: content.timestamp, // 
            nonceStr: content.nonceStr, // 
            signature: content.signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
          })

          WX.ready(function () {
            // configreadyconfigconfigreadyready
            WX.chooseWXPay({
              timestamp: content.timestamp, // jssdktimestamptimeStampS
              nonceStr: content.nonceStr, //  32 
              package: content.package, // prepay_idprepay_id=***
              signType: 'MD5', // 'SHA1''MD5'
              paySign: content.paySign, // 
              success: function (res) {
                // 
                callback('')
              }
            })
          })
          WX.error(function (res) {
            callback('')
            // configerrorconfigdebugresSPA
          })
        }
      }
    )
  },
  evaluateOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'phone': obj.phone,
      'score': obj.score,
      'content': obj.content,
      'driverId': obj.driverId
    }
    var url = InitData.weixinUrl + '/api/daijia/review'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/daijia/getReview'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  }
}



