import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  queryCar (obj, callback) {  // 
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey,
      'passengerId': obj.passengerId,
      'lat': obj.lat,
      'lng': obj.lng
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/getCarTypesV4'
    Vue.http.get(url, {params}).then(
      data => {
        var Data = data.body
        const ZcData = {
          carInfo: {
            allNum: 1,
            maxNum: 10,
            data: []
          }
        }
        for (let i = 0; i < Data.length; i++) {
          var obj = {
            name: Data[i].typeName,
            id: Data[i].typeId,
            serverTypes: Data[i].serverTypes,
            num: 0
          }
          if (i === 0) {
            obj.num = 1
          }
          ZcData.carInfo.data.push(obj)
        }
        callback(ZcData.carInfo)
      }
    )
  },
  createZcOrder (params, callback) {
    params = Vue.prototype.$utils.objKeySort('/passenger/api/v1/createSpecialOrder', params)
    let url = InitData.serverUrl + '/createSpecialOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  findZcOrderById (params, callback) {
    params = Vue.prototype.$utils.objKeySort('/api/v1/finishSpecialOrder', params)
    let url = InitData.weixinUrl + '/querySpecialOrderById'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  v5ZcBalancePay (params, callback) { // v5 
    params = Vue.prototype.$utils.objKeySort('/api/v1/finishSpecialOrder', params)
    let url = InitData.weixinUrl + '/finishSpecialOrder'
    Vue.http.put(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  v5ZcEvaluateOrder (params, callback) {  // v5 
    params = Vue.prototype.$utils.objKeySort('/api/v1/rateOrder', params)
    let url = InitData.serverUrl + '/rateOrder'
    Vue.http.put(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  v5ZcCancelOrder (params, callback) { // v5 
    params = Vue.prototype.$utils.objKeySort('/passenger/api/v1/cancelOrder', params)
    let url = InitData.serverUrl + '/cancelOrder'
    Vue.http.put(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  v5getZcCoupons (params, callback) { // v5
    params = Vue.prototype.$utils.objKeySort('/passenger/api/v1/coupons', params)
    if (params.passenger_id) {
      let url = InitData.serverUrl + '/coupons'
      Vue.http.get(url, {params}).then(
        data => {
          callback(data.body)
        }
      )
    }
  },
  queryWaiter (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'carTypes': obj.carTypes,
      'lat': obj.lat,
      'lng': obj.lng
    }
    var url = InitData.weixinUrl + '/v5/zhuanche/coutNearDrivers'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body.data && data.body.data.countSun ? data.body.data.countSun : 0)
      }
    )
  },
  budgetCost (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'areaIdAndTypeIds': obj.areaIdAndTypeIds,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'endLat': obj.endLat,
      'endLng': obj.endLng
    }
    var url = InitData.weixinUrl + '/v5/zhuanche/calculationPrice'
    Vue.http.get(url, {params}).then(data => {
      callback(data.body.data)
    })
  },
  ZcBussinessType (params, callback) { // v5APP
    params = Vue.prototype.$utils.objKeySort('/api/v1/specialcar/bustree', params)
    let url = '/api/v1/specialcar/bustree'
    Vue.http.get(url, {params}).then(data => {
      callback(data.body)
    })
  },
  getV5ZcBudgetCost (params, callback) { // V5
    params = Vue.prototype.$utils.objKeySort('/api/v1/getSpecialBudgetPrice', params)
    let url = '/api/v1/getSpecialBudgetPrice'
    Vue.http.get(url, {params}).then(data => {
      callback(data.body)
    })
  },
  v5GetZcOrderRecord (params, callback) { // v5
    params = Vue.prototype.$utils.objKeySort('/passenger/api/v1/orders', params)
    let url = InitData.serverUrl + '/orders'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
// budgetCost (params, callback) { // 
//   params = Vue.prototype.$utils.objKeySort('/v5/zhuanche/calculationPrice', params)
//   let url = '/v5/zhuanche/calculationPrice'
//   Vue.http.get(url, {params}).then(
//     data => {
//       callback(data.body)
//     }
//   )
// },
  makeAnAppointment (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey || null,
      'companyId': obj.companyId,
      'companyName': obj.companyName,
      'passengerId': obj.passengerId,
      'passengerName': obj.passengerName,
      'passengerPhone': obj.passengerPhone,
      'serverTime': obj.serverTime,
      'userPhone': obj.userPhone,
      'startAddress': obj.startAddress,
      'startLat': obj.startLat,
      'mileage': obj.mileage,
      'startLng': obj.startLng,
      'endAddress': obj.endAddress,
      'endLat': obj.endLat,
      'endLng': obj.endLng,
      'trainNo': obj.trainNo,
      'carTypes': obj.carTypes,
      'serviceType': obj.serviceType,
      'channelName': obj.channelName,
      'flightNo': obj.flightNo
    }

    var url = InitData.weixinUrl + '/v5/zhuanche/createOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  payInfo (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/prepay'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  pay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'payType': obj.payType
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/doPay'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  payState (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/hasPayed'
    Vue.http.get(url, params).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  coupon (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/findCouponV4'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  awaitWaiter (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/findOrder'
    Vue.http.get(url, {params, _timeout: 5000}).then(
      data => {
        // console.log(data)
        callback(data.body)
      }
    )
  },
  cancelOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'reson': obj.reson
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanche/cancelOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        // console.log(data)
        callback(data.body)
      }
    )
  },
  evaluateOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'content': obj.content,
      'score': obj.score
    }
    var url = InitData.weixinUrl + '/api/zhuanche/review'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/zhuanche/getReview'
    Vue.http.get(url, {params, _timeout: 5000}).then(
      data => {
        // console.log(data)
        callback(data.body)
      }
    )
  },
  weixinPay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/zhuanche/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // ,apialertpclogpc
            appId: window.configure.WXappId, // 
            timeStamp: content.timestamp, // 
            nonceStr: content.nonceStr, // 
            signature: content.signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
          })

          WX.ready(function () {
            // configreadyconfigconfigreadyready
            WX.chooseWXPay({
              timestamp: content.timestamp, // jssdktimestamptimeStampS
              nonceStr: content.nonceStr, //  32 
              package: content.package, // prepay_idprepay_id=***
              signType: 'MD5', // 'SHA1''MD5'
              paySign: content.paySign, // 
              success: function (res) {
                // 
                callback('')
              }
            })
          })
          WX.error(function (res) {
            callback('')
            // configerrorconfigdebugresSPA
          })
        }
      }
    )
  }
}



