import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  paotuType (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      companyId: '',
      employToken: ''
    }
    var url = InitData.serverUrl + '/news/weixin'
    Vue.http.get(url, params).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  budgetCost (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      areaId: obj.areaId,
      time: obj.time,
      mileage: obj.mileage
    }
    var url = InitData.weixinUrl + '/v5/paotui/calculationPrice'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/v5/paotui/getCoupons'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  placeAnOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey,
      'prePay': false,
      'payType': 'balance', // balance  sign 
      'passengerId': obj.passengerId,
      'passengerName': obj.passengerName,
      'passengerPhone': obj.passengerPhone,
      'companyName': obj.companyName,
      'companyId': obj.companyId,
      'channelName': obj.channelName,
      'startAddress': obj.startAddress,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'deliverAddress': obj.deliverAddress,
      'deliverLat': obj.deliverLat,
      'deliverLng': obj.deliverLng,
      'startTime': obj.startTime,
      'startPrice': obj.startPrice,
      'mileagePrice': obj.mileagePrice,
      'travelTimePrice': obj.travelTimePrice,
      'mileage': obj.mileage,
      'travelTime': obj.travelTime,
      'shouldPay': obj.shouldPay,
      'couponId': obj.couponId,
      'photos': obj.photos,
      'orderTypeId': obj.orderTypeId,
      'content': obj.content,
      'recipients': obj.recipients,
      'phone': obj.phone,
      'weight': obj.weight,
      'buyNeeds': obj.buyNeeds,
      'goodsCash': obj.goodsCash,
      'thanksCash': obj.thanksCash,
      'addServices': obj.addServices,
      'tips': obj.tips,
      'startDetailed': obj.startDetailed,
      'endDetailed': obj.endDetailed
    }
    var url = InitData.weixinUrl + '/v5/paotui/createByPassenger'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data && data.body.success && obj.fn) {
          obj.fn(data.body.orderId)
        } else {
          obj.fn(false)
        }
        callback(data.body)
      }
    )
  },
  queryOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }

    function ajax () {
      var url = InitData.weixinUrl + '/api/errand/v2/orders/findOne'
      Vue.http.get(url, {params}).then(
        data => {
          callback(data.body)
        },
        data => {
          ajax()
        }
      )
    }

    ajax()
  },
  payData (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      orderId: obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/pt/prepay'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  toPay (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'payType': obj.payType // balance  sign 
    }
    var url = InitData.weixinUrl + '/h5/v4/pt/doPay'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  review (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'score': obj.score,
      'content': obj.content
    }
    var url = InitData.weixinUrl + '/api/errand/v2/orders/review'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  cancelOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'reson': obj.reson
    }
    var url = InitData.weixinUrl + '/h5/v4/pt/cancelOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      orderId: obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/paotui/getReview'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  weixinPay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/paotui/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // ,apialertpclogpc
            appId: window.configure.WXappId, // 
            timeStamp: content.timestamp, // 
            nonceStr: content.nonceStr, // 
            signature: content.signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
          })

          WX.ready(function () {
            // configreadyconfigconfigreadyready
            WX.chooseWXPay({
              timestamp: content.timestamp, // jssdktimestamptimeStampS
              nonceStr: content.nonceStr, //  32 
              package: content.package, // prepay_idprepay_id=***
              signType: 'MD5', // 'SHA1''MD5'
              paySign: content.paySign, // 
              success: function (res) {
                // 
                callback('')
              }
            })
          })
          WX.error(function (res) {
            callback('')
            // configerrorconfigdebugresSPA
          })
        }
      }
    )
  }
}



