import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  queryCarType (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'companyId': obj.companyId
      // 'acKey': InitData.acKey,
      // 'lng': obj.lng,
      // 'lat': obj.lat
    }
    var url = InitData.weixinUrl + '/api/freight/queryTruckType'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  budgetCost (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'companyId': obj.companyId,
      'areaId': obj.areaId,
      'time': obj.time,
      'mileage': obj.mileage
    }
    var url = InitData.weixinUrl + '/api/freight/getPrice'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  placeAnOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'passengerName': obj.passengerName, // 
      'passengerPhone': obj.passengerPhone, // s
      'truckTypeId': obj.truckTypeId, // Id
      'truckTypeName': obj.truckTypeName, // 
      'startTime': obj.startTime, // 
      'waypoint': obj.waypoint ? obj.waypoint : null, // 
      'preStartPrice': obj.preStartPrice, // 
      'preMileage': obj.preMileage, // 
      'preRunTime': obj.preRunTime, // 
      'preRunTimePrice': obj.preRunTimePrice, // 
      'budgetPay': obj.budgetPay, // 
      'passengerId': obj.passengerId,
      'companyId': obj.companyId,
      'companyName': obj.companyName,
      'memo': obj.memo,
      'flitting': obj.flitting, // 
      'receipt': obj.receipt, // 
      'receivedPay': obj.receivedPay, // 
      'payMount': obj.payMount, // 
      'couponId': obj.couponId ? obj.couponId : null,
      'preMileagePrice': obj.preMileagePrice, // 
      'payDetail': obj.payDetail, //  
      'channelName': '', // 
      'tip': obj.tip // 
    }
    var url = InitData.weixinUrl + '/api/freight/create'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { //  
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/v5/huoyun/getCoupons'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/api/freight/findByOrderId'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  cancelOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'cause': obj.cause
    }
    var url = InitData.weixinUrl + '/api/freight/cancel'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  payInfo (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/api/freight/clientPayData'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  weixinPay (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/huoyun/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // ,apialertpclogpc
            appId: window.configure.WXappId, // 
            timeStamp: content.timestamp, // 
            nonceStr: content.nonceStr, // 
            signature: content.signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
          })

          WX.ready(function () {
            // configreadyconfigconfigreadyready
            WX.chooseWXPay({
              timestamp: content.timestamp, // jssdktimestamptimeStampS
              nonceStr: content.nonceStr, //  32 
              package: content.package, // prepay_idprepay_id=***
              signType: 'MD5', // 'SHA1''MD5'
              paySign: content.paySign, // 
              success: function (res) {
                // 
                callback('')
              }
            })
          })
          WX.error(function (res) {
            callback('')
            // configerrorconfigdebugresSPA
          })
        }
      }
    )
  },
  balancePay (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'who': obj.who,
      'payType': obj.payType
    }
    var url = InitData.weixinUrl + '/api/freight/finishTask'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  evaluateOrder (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'content': obj.content,
      'score': obj.score
    }
    var url = InitData.weixinUrl + '/api/freight/review'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/huoyun/getReview'
    Vue.http.get(url, {params, _timeout: 5000}).then(
      data => {
        callback(data.body)
      }
    )
  },
  allOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId,
      'page': obj.page,
      'rows': obj.rows
    }
    Vue.http.get(InitData.weixinUrl + '/api/freight/findOrders', {params}).then((data) => {
      callback(data.body)
    })
  },
  unfilledOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    Vue.http.get(InitData.weixinUrl + '/v5/huoyun/getNotSuccessOrders', {params}).then((data) => {
      callback(data.body)
    })
  }
}



