import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  lineStartCity (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/findStartCitys'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  lineEndCity (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'startCity': obj.startCity,
      'startAddress': obj.startAddress
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/findEndByStart'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  budgetMoney (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'areaId': obj.areaId,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'endLat': obj.endLat,
      'endLng': obj.endLng
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/budgetedExpense'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { //  
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/findZXCouponV4'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  findOrder (obj, callback) { //  
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/findZXOrder'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  payInfo (obj, callback) { //  
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/prepay'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  makeAnAppointment (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey,
      'orderType': obj.orderType, // 0  1  2 
      'passengerId': obj.passengerId,
      'passengerPhone': obj.passengerPhone,
      'lineId': obj.lineId,
      'startAddress': obj.startAddress,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'endAddress': obj.endAddress,
      'endLat': obj.endLat,
      'lat': obj.lat,
      'lng': obj.lng,
      'endLng': obj.endLng,
      'startTime': obj.startTime,
      'budgetPay': obj.budgetPay,
      'memo': obj.memo,
      'couponId': obj.couponId,
      'peopleNumber': obj.peopleNumber,
      'takeDeliveryName': obj.takeDeliveryName,
      'takeDeliveryPhone': obj.takeDeliveryPhone,
      'openId': obj.openId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/makeAnZXAppointmentV5'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  toPay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'payType': obj.payType
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/doPay'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  review (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'score': obj.score,
      'content': obj.content
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/review'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  cancelOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'reson': obj.reson
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/cancelOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/getReview'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  weixinPay (obj, callback) { // 
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // ,apialertpclogpc
            appId: window.configure.WXappId, // 
            timeStamp: content.timestamp, // 
            nonceStr: content.nonceStr, // 
            signature: content.signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
          })

          WX.ready(function () {
            // configreadyconfigconfigreadyready
            WX.chooseWXPay({
              timestamp: content.timestamp, // jssdktimestamptimeStampS
              nonceStr: content.nonceStr, //  32 
              package: content.package, // prepay_idprepay_id=***
              signType: 'MD5', // 'SHA1''MD5'
              paySign: content.paySign, // 
              success: function (res) {
                // 
                callback('')
              }
            })
          })
          WX.error(function (res) {
            callback('')
            // configerrorconfigdebugresSPA
          })
        }
      }
    )
  }
}



