import Vue from 'vue'
import Router from 'vue-router'
import Project from '@/components/project/project' // 
import BannerContent from '@/components/project/bannerContent' // 
import DaiJia from '@/components/project/daijia/daijia' // 
import DaiJiaAwait from '@/components/project/daijia/await' //  
import DaiJiaService from '@/components/project/daijia/service' //  
import DaiJiaSettlement from '@/components/project/daijia/settlement' //  
import DaiJiaAffirm from '@/components/project/daijia/affirm' //  
import DaiJiaCheckOrder from '@/components/project/daijia/checkOrder' //  
import PaoTui from '@/components/project/paotui/paotui' // 
import PaoDownOrder from '@/components/project/paotui/downOrder' //  
import PaoAwait from '@/components/project/paotui/await' //  
import PaoServer from '@/components/project/paotui/server' //  
import PaoSettlement from '@/components/project/paotui/settlement' //  
import PaoAffirm from '@/components/project/paotui/affirm' //  
import PaoCheckOrder from '@/components/project/paotui/checkOrder' //  
import HuoYun from '@/components/project/huoyun/huoyun' // 
import HuoYunOrder from '@/components/project/huoyun/orderInfo' // 
import HuoYunAwait from '@/components/project/huoyun/await' // 
import HuoYunService from '@/components/project/huoyun/service' //  
import HuoYunSettlement from '@/components/project/huoyun/settlement' //  
import HuoYunAffirm from '@/components/project/huoyun/affirm' //  
import HuoYunCheckOrder from '@/components/project/huoyun/checkOrder' //  
import ZhuanChe from '@/components/project/zhuanche/zhuanche' // 
import ZhuanCheAwait from '@/components/project/zhuanche/await' //  
import ZhuanCheService from '@/components/project/zhuanche/service' //  
import ZhuanCheSettlement from '@/components/project/zhuanche/settlement' //  
import ZhuanCheAffirm from '@/components/project/zhuanche/affirm' //   
import ZhuanCheCheckOrder from '@/components/project/zhuanche/checkOrder' //  
import ZhuanXian from '@/components/project/zhuanxian/zhuanxian' // 
import ZhuanXianAffirm from '@/components/project/zhuanxian/affirm' // 
import ZhuanXianAwait from '@/components/project/zhuanxian/await' // 
import ZhuanXianService from '@/components/project/zhuanxian/service' // 
import ZhuanXianSettlement from '@/components/project/zhuanxian/settlement' // 
import ZhuanXianCheckOrder from '@/components/project/zhuanxian/checkOrder' // 
import Inform from '@/components/inform/inform' // 
import qrCode from '@/components/qrCode/qrCode' // 
import friend from '@/components/friend/friend' // 
import Settings from '@/components/settings/settings' // 
import serviceAgreement from '@/components/settings/serviceAgreement' // 
import servicePrice from '@/components/settings/servicePrice' // 
import CompileNom from '@/components/compileNom/compileNom' // 
import MeWallet from '@/components/mewallet/mewallet' // 
import Invoice from '@/components/mewallet/invoice/invoice' // 
import invoiceApply from '@/components/mewallet/invoice/invoiceApply' // 
import InvoiceRecord from '@/components/mewallet/invoice/invoiceRecord' // 
import invoiceOrderRecord from '@/components/mewallet/invoice/invoiceOrderRecord' // 
import CapitalRecord from '@/components/mewallet/capitalRecord/capitalRecord' // 
import Recharge from '@/components/recharge/recharge' // 
import Coupon from '@/components/mewallet/coupon/coupon' // 
import CouponStale from '@/components/mewallet/coupon/stale' //  
import CouponUnused from '@/components/mewallet/coupon/unused' //  
import CouponUsed from '@/components/mewallet/coupon/used' //  
import OrderRecord from '@/components/orderRecord/orderRecord' // 
import RecordDaiJia from '@/components/orderRecord/daijia' //  
import RecordYongChe from '@/components/orderRecord/yongche' //  
import RecordPaoTui from '@/components/orderRecord/paotui' //  
import RecordZhuanXian from '@/components/orderRecord/zhuanxian' //  
import RecordHuoyun from '@/components/orderRecord/huoyun' //  

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Project',
      component: Project,
      children: [
        {
          path: 'daijia',
          name: 'DaiJia',
          component: DaiJia
        },
        {
          path: 'paotui',
          name: 'PaoTui',
          component: PaoTui
        },
        {
          path: 'zhuanche',
          name: 'ZhuanChe',
          component: ZhuanChe
        },
        {
          path: 'zhuanxian',
          name: 'ZhuanXian',
          component: ZhuanXian
        },
        {
          path: 'huoyun',
          name: 'huoyun',
          component: HuoYun
        }
      ]
    },
    {
      path: '/inform',
      name: 'Inform',
      component: Inform
    },
    {
      path: '/qrCode',
      name: 'qrCode',
      component: qrCode
    },
    {
      path: '/friend',
      name: 'friend',
      component: friend
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings
    },
    {
      path: '/serviceAgreement',
      name: 'serviceAgreement',
      component: serviceAgreement
    },
    {
      path: '/servicePrice',
      name: 'servicePrice',
      component: servicePrice
    },
    {
      path: '/mewallet',
      name: 'MeWallet',
      component: MeWallet
    },
    {
      path: '/invoice',
      name: 'Invoice',
      component: Invoice
    },
    {
      path: '/invoiceRecord',
      name: 'InvoiceRecord',
      component: InvoiceRecord
    },
    {
      path: '/invoiceApply',
      name: 'invoiceApply',
      component: invoiceApply
    },
    {
      path: '/invoiceOrderRecord',
      name: 'invoiceOrderRecord',
      component: invoiceOrderRecord
    },
    {
      path: '/coupon',
      name: 'coupon',
      component: Coupon,
      children: [
        {
          path: 'stale',
          name: 'CouponStale',
          component: CouponStale
        },
        {
          path: 'used',
          name: 'CouponUsed',
          component: CouponUsed
        },
        {
          path: 'nused',
          name: 'CouponUnused',
          component: CouponUnused
        }
      ]
    },
    {
      path: '/compileNom',
      name: 'CompileNom',
      component: CompileNom
    },
    {
      path: '/capitalRecord',
      name: 'CapitalRecord',
      component: CapitalRecord
    },
    {
      path: '/recharge',
      name: 'Recharge',
      component: Recharge
    },
    {
      path: '/daijia/await',
      name: 'DaiJiaAwait',
      component: DaiJiaAwait
    },
    {
      path: '/daijia/service',
      name: 'DaiJiaService',
      component: DaiJiaService
    },
    {
      path: '/daijia/settlement',
      name: 'DaiJiaSettlement',
      component: DaiJiaSettlement
    },
    {
      path: '/daijia/affirm',
      name: 'DaiJiaAffirm',
      component: DaiJiaAffirm
    },
    {
      path: '/daijia/checkOrder',
      name: 'DaiJiaCheckOrder',
      component: DaiJiaCheckOrder
    },
    {
      path: '/huoyun/orderInfo',
      name: 'HuoYunOrder',
      component: HuoYunOrder
    },
    {
      path: '/huoyun/await',
      name: 'HuoYunAwait',
      component: HuoYunAwait
    },
    {
      path: '/huoyun/service',
      name: 'HuoYunService',
      component: HuoYunService
    },
    {
      path: '/huoyun/settlement',
      name: 'HuoYunSettlement',
      component: HuoYunSettlement
    },
    {
      path: '/huoyun/affirm',
      name: 'HuoYunAffirm',
      component: HuoYunAffirm
    },
    {
      path: '/huoyun/checkOrder',
      name: 'HuoYunCheckOrder',
      component: HuoYunCheckOrder
    },
    {
      path: '/zhuanche/await',
      name: 'ZhuanCheAwait',
      component: ZhuanCheAwait
    },
    {
      path: '/zhuanche/affirm',
      name: 'ZhuanCheAffirm',
      component: ZhuanCheAffirm
    },
    {
      path: '/zhuanche/service',
      name: 'ZhuanCheService',
      component: ZhuanCheService
    },
    {
      path: '/zhuanche/settlement',
      name: 'ZhuanCheSettlement',
      component: ZhuanCheSettlement
    },
    {
      path: '/zhuanche/checkOrder',
      name: 'ZhuanCheCheckOrder',
      component: ZhuanCheCheckOrder
    },
    {
      path: '/orderRecord',
      name: 'OrderRecord',
      component: OrderRecord,
      children: [
        {
          path: '/orderRecord/daijia',
          name: 'RecordDaiJia',
          component: RecordDaiJia
        },
        {
          path: '/orderRecord/yongche',
          name: 'RecordYongChe',
          component: RecordYongChe
        },
        {
          path: '/orderRecord/paotui',
          name: 'RecordPaoTui',
          component: RecordPaoTui
        },
        {
          path: '/orderRecord/zhuanxian',
          name: 'RecordZhuanXian',
          component: RecordZhuanXian
        },
        {
          path: '/orderRecord/huoyun',
          name: 'RecordHuoyun',
          component: RecordHuoyun
        }
      ]
    },
    {
      path: '/paotui/downOrder',
      name: 'PaoDownOrder',
      component: PaoDownOrder
    },
    {
      path: '/paotui/await',
      name: 'PaoAwait',
      component: PaoAwait
    },
    {
      path: '/paotui/server',
      name: 'PaoServer',
      component: PaoServer
    },
    {
      path: '/paotui/settlement',
      name: 'PaoSettlement',
      component: PaoSettlement
    },
    {
      path: '/paotui/affirm',
      name: 'PaoAffirm',
      component: PaoAffirm
    },
    {
      path: '/paotui/checkOrder',
      name: 'PaoCheckOrder',
      component: PaoCheckOrder
    },
    {
      path: '/bannerContent',
      name: 'BannerContent',
      component: BannerContent
    },
    {
      path: '/zhuanxian/affirm',
      name: 'ZhuanXianAffirm',
      component: ZhuanXianAffirm
    },
    {
      path: '/zhuanxian/await',
      name: 'ZhuanXianAwait',
      component: ZhuanXianAwait
    },
    {
      path: '/zhuanxian/service',
      name: 'ZhuanXianService',
      component: ZhuanXianService
    },
    {
      path: '/zhuanxian/settlement',
      name: 'ZhuanXianSettlement',
      component: ZhuanXianSettlement
    },
    {
      path: '/zhuanxian/checkOrder',
      name: 'ZhuanXianCheckOrder',
      component: ZhuanXianCheckOrder
    },
    {path: '*', redirect: '/'}
  ]
})



